import React from "react";
import "./styles.css";

const Card = ({ data }) => {
  {
    return (
      <>
        {data.map((item) => {
          return (
            <div className="card-container" key={item.id}>
              <h2>{item.title}</h2>
              <h4>{item.category}</h4>
              <div>{item.description}</div>
              <div>
                <img alt="food-img" src={item.image} />
              </div>
            </div>
          );
        })}
      </>
    );
  }
};

export default Card;
