import { useState } from "react";
import "./App.css";
import Card from "./components/Card/Card";
import mealApi from "./components/api/mealApi";

function App() {
  const [data, setData] = useState(mealApi);

  const handleClick = (category) => {
    let filteredMeals = [];
    if (category === "All") {
      filteredMeals = mealApi;
    } else {
      filteredMeals = mealApi.filter((meal) => {
        return meal.category === category;
      });
    }

    setData(filteredMeals);
  };
  return (
    <div className="App">
      <div>
        <h2>Food Items</h2>
      </div>
      <div className="menu-bar">
        <button
          onClick={(e) => {
            handleClick("Breakfast");
          }}
        >
          Breakfast
        </button>
        <button
          onClick={(e) => {
            handleClick("Lunch");
          }}
        >
          Lunch
        </button>
        <button
          onClick={(e) => {
            handleClick("Dinner");
          }}
        >
          Dinner
        </button>
        <button
          onClick={(e) => {
            handleClick("All");
          }}
        >
          All
        </button>
      </div>
      <hr />
      <Card data={data} />
    </div>
  );
}

export default App;
